import apiAuthorization from '../api/apiAuthorization';
import Router from '../utilities/router';
//import { showPreloader, hidePreloader } from '../utils/preloader';
//import { handleError } from '../utils/apiHandler';
//import { IAuthApiSignIn, IAuthApiSignUp } from '../interfaces/IAuthApi';
//import { store } from '../store';
//import { showToast } from '../utils/toast';

interface AuthorizationSignInInterface {
	login: string
	password: string
}

interface AuthorizationSignUpInterface {
	firstName: string
	secondName: string
	login: string
	email: string
	phone: string
	password: string
}

const router = new Router('#app');

class AuthorizationController {
	public signIn(user: AuthorizationSignInInterface) {
		//showPreloader();
		return apiAuthorization.signIn(user)
		.then(() => {
			//showToast('Вы вошли', 'success');
			router.go('/');
		})
		.catch(() => {
			router.go('/500');
			//hidePreloader();
		});
	}

	public signUp(user: AuthorizationSignUpInterface) {
		//showPreloader();
		return apiAuthorization.signUp(user)
		.then(() => {
			//showToast('Вы зарегистрировались', 'success');
			router.go('/messenger');
		})
		.catch(() => {
			router.go('/500');
			//hidePreloader();
		});
	}

	public signOut() {
		return apiAuthorization.signOut()
		.then(() => {
			//localStorage.removeItem('last-select-chat-id');
			router.go('/authorization');
		});
	}

	public checkAuth() {
		return apiAuthorization.checkAuth()
		.then((user) => {
			console.log(user)
			//store.setState({
			//  currentUser: user,
			//});
		})
		.catch((error) => {
			console.log(error)
			//handleError(error);
			router.go('/sign-in');
		});
	}
}

export default new AuthorizationController();